import { Component, Element, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';

/**
 * @slot content - for the content of the expand component
 */

@Component({
  tag: 'mdx-expand',
  styleUrl: 'expand.scss',
  shadow: true,
})
export class Expand {
  @Element()
  host: HTMLMdxExpandElement;

  @Prop({ reflect: true })
  expanded: boolean;

  @Prop()
  labelOpen: string;

  @Prop()
  labelClosed: string;

  @Prop()
  lineClamp: number;

  componentWillRender() {
    this.host.style.setProperty('--expand-line-clamp', this.lineClamp > 1 ? this.lineClamp.toString() : '');
  }

  render() {
    return (
      <div
        class={`expand${this.expanded === undefined ? '' : this.expanded === true ? ' expand--is-expanded' : ' expand--is-collapsed'}`}
      >
        <div class="expand__content">
          <slot />
        </div>
        <button
          class="expand__button"
          onClick={() => {
            if (this.expanded) {
              const height = this.host.shadowRoot?.querySelector('.expand__content')?.clientHeight;
              if (typeof height === 'number') {
                this.host.style.setProperty('--expand-height', `${height}px`);
              } else {
                this.host.style.setProperty('--expand-height', '100vh');
              }
            }
            this.expanded = !this.expanded;
          }}
        >
          {this.expanded ? this.labelOpen : this.labelClosed}
          <mdx-icon-chevron-down class="expand__icon" size={ICON_SIZES.XS} />
        </button>
      </div>
    );
  }
}
